@charset "UTF-8";
/* ============= Fonts */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}
/* ================== Global styles */
*,
*:before,
*:after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: 1440px;
    padding: 0 40px;
  }
}

.page,
.page-body {
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  height: 100%;
  margin: 0;
}

/* Прижимаем footer к низу */
.page-body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

/* Прижимаем footer к низу */
.main {
  flex: 1 1 auto;
}

/* ================== Header */
.header {
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  letter-spacing: 0.24px;
  text-transform: uppercase;
  position: relative;
  background-color: #ffffff;
}

.header__wrapper {
  display: grid;
  grid-template-columns: min-content 1fr min-content;
  align-items: center;
  padding: 0 0 0 20px;
}
@media (min-width: 768px) {
  .header__wrapper {
    width: 768px;
    padding: 0 20px;
    grid-template-columns: min-content 1fr;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    width: 1440px;
    padding: 0 40px;
  }
}

.header__logo {
  display: block;
}

.header__logo-desktop,
.header__logo-tablet {
  display: none;
}

.header__logo-image {
  width: 32px;
  height: 36px;
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: end;
  flex-wrap: wrap;
}

.header__logo-heading {
  display: none;
}
@media (min-width: 1440px) {
  .header__logo-heading {
    display: block;
    font-weight: 400;
    text-transform: none;
    min-width: 135px;
    letter-spacing: normal;
    margin: 0;
  }
}

/* =============== Tablet */
@media (min-width: 767px) {
  .header__logo {
    display: none;
  }
  .header__logo-tablet {
    display: block;
    line-height: 0;
    margin-right: 67px;
  }
  .header__logo-image--tablet {
    width: 82px;
    height: 36px;
  }
  .nav {
    display: flex;
    align-items: center;
    justify-content: end;
  }
  .nav__list {
    justify-content: flex-start;
  }
}
/* ================ Desktop */
@media (min-width: 1440px) {
  .header__logo-block {
    display: flex;
    align-items: center;
  }
  .header__logo-tablet {
    display: none;
  }
  .header__logo-desktop {
    display: block;
    margin-right: 20px;
  }
  .header__logo-image--desktop {
    width: 170px;
    height: 36px;
  }
  .nav {
    justify-content: space-between;
  }
}
/* =========Burger */
.burger {
  border: none;
  cursor: pointer;
  height: 60px;
  width: 60px;
  padding: 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

.burger:active {
  background-color: #f0ebff;
}

.burger__menu {
  display: block;
  position: relative;
  width: 16px;
  height: 1.5px;
  background-color: #333333;
  margin: 0 auto;
  transition: height 0.2s ease;
}

.burger__menu::after,
.burger__menu::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 1.5px;
  border: none;
  background-color: #333333;
  left: 0;
  transition: top 0.3s ease, transform 0.3s ease;
}

.burger__menu::after {
  top: 5px;
}

.burger__menu::before {
  top: -5px;
}

.burger__menu.open {
  height: 0;
}

.burger__menu.open.burger__menu::after {
  transform: rotate(45deg);
  top: 1px;
}

.burger__menu.open.burger__menu::before {
  transform: rotate(-45deg);
  top: 1px;
}

/* ========= Site-navigation */
.site-navigation {
  flex-direction: column;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 100%;
  background-color: #ffffff;
  transition: all 0.5s ease-in-out;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
}
@media (min-width: 768px) {
  .site-navigation {
    position: relative;
    z-index: auto;
    visibility: visible;
    opacity: 1;
    background-color: transparent;
    flex-direction: row;
    margin-right: 61px;
  }
}
@media (min-width: 1440px) {
  .site-navigation {
    margin-right: 0;
    margin-left: 153px;
  }
}

.site-navigation.open {
  z-index: 5;
  opacity: 1;
  visibility: visible;
}

.site-navigation__item {
  border-top: 1px solid #f0ebff;
}
@media (min-width: 768px) {
  .site-navigation__item {
    border-top: none;
  }
}
@media (min-width: 1440px) {
  .site-navigation__item {
    margin-right: 20px;
  }
}

.site-navigation__item:last-child {
  border-bottom: 1px solid #f0ebff;
}
@media (min-width: 768px) {
  .site-navigation__item:last-child {
    border-bottom: none;
  }
}
@media (min-width: 1440px) {
  .site-navigation__item:last-child {
    margin-right: 0;
  }
}

.site-navigation__link {
  display: block;
  text-decoration: none;
  color: #333333;
  padding: 20px;
}
@media (min-width: 768px) {
  .site-navigation__link {
    padding: 30px 20px;
  }
}

.site-navigation__link:active {
  background-color: #f0ebff;
}

/* ============ Tablet */
@media (min-width: 767px) {
  .site-navigation__link--active {
    color: #7859cf;
    border-bottom: 2px solid #7859cf;
  }
  .site-navigation__link:hover,
.site-navigation__link:focus {
    color: #7859cf;
  }
  .site-navigation__link:active {
    background-color: #f0ebff;
  }
}
/* ========= User-navigation */
.user-navigation__link {
  display: block;
  height: 60px;
  padding: 22px;
  text-decoration: none;
}
@media (min-width: 768px) {
  .user-navigation__link {
    font-size: 0;
    line-height: 0;
    height: auto;
    padding: 32px 22px;
  }
}
@media (min-width: 1440px) {
  .user-navigation__link {
    display: flex;
    padding: 30px 20px;
  }
}

.user-navigation__text {
  display: none;
}
@media (min-width: 1440px) {
  .user-navigation__text {
    display: block;
    font-size: 12px;
    line-height: 16px;
    color: #333333;
  }
}

.user-navigation__link:active {
  background-color: #f0ebff;
}

.user-navigation__icon {
  fill: #333333;
}
@media (min-width: 1440px) {
  .user-navigation__icon {
    margin-right: 8px;
  }
}

/* =============== Desktop */
@media (min-width: 1440px) {
  .user-navigation__item {
    margin-right: 20px;
  }
  .user-navigation__item:last-child {
    margin-right: 0;
  }
  /* Изменение цвета у текста при наведении */
  .user-navigation__link:hover .user-navigation__text,
.user-navigation__link:focus .user-navigation__text {
    color: #7859cf;
  }
  /* Изменение цвета у иконки при наведении */
  .user-navigation__link:hover .user-navigation__icon,
.user-navigation__link:focus .user-navigation__icon {
    fill: #7859cf;
  }
}
/* =============== Реализация без использования JS. Главное меню в мобильной версии. Добавить/убрать класс header--nojs у тега header */
@media (max-width: 767px) {
  .header.header--nojs .header__wrapper {
    position: relative;
    display: block;
    padding: 0;
  }
  .header.header--nojs .header__logo-block {
    position: absolute;
    top: 12px;
    left: 20px;
  }
  .header.header--nojs .header__nav {
    display: flex;
    flex-direction: column;
  }
  .header.header--nojs .site-navigation {
    position: static;
    display: block;
    visibility: visible;
    opacity: 1;
    z-index: 0;
  }
  .header.header--nojs .user-navigation {
    order: -1;
    margin-left: 75px;
  }
  .header.header--nojs .header__burger {
    display: none;
  }
}
