/* ========== Promo-buttons */
.promo__button-prev,
.promo__button-next {
  position: absolute;
  height: 100%;
  width: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  top: 0;
}
@media (min-width: 768px) {
  .promo__button-prev,
.promo__button-next {
    width: 80px;
    z-index: 5;
  }
}

.promo__button-prev {
  left: 0;
}

.promo__button-next {
  right: 0;
}

.promo__button-prev::before,
.promo__button-next::before {
  content: "";
  position: absolute;
  background-image: url("../img/decorative/arrow-promo.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  width: 11px;
  height: 20px;
}

.promo__button-prev::before {
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .promo__button-prev::before {
    right: 35px;
    top: 320px;
  }
}
@media (min-width: 1440px) {
  .promo__button-prev::before {
    top: 357px;
  }
}

.promo__button-next::before {
  transform: rotate(180deg) translateY(-50%);
  left: 5px;
  top: 300px;
}
@media (min-width: 768px) {
  .promo__button-next::before {
    left: 35px;
  }
}
@media (min-width: 1440px) {
  .promo__button-next::before {
    top: 336px;
  }
}

.promo__button-prev:hover,
.promo__button-prev:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}

.promo__button-next:hover,
.promo__button-next:focus {
  background: linear-gradient(270deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}

.promo__button-prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

.promo__button-next:active {
  background: linear-gradient(270deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}

.promo__button-prev:disabled.promo__button-prev::before {
