/* ============= Country filter */
.filter__label--country {
  padding: 2px 0 2px 52px;
}

.filter__label--country::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 24px;
  border-radius: 12px;
  background: #bdbdbd;
  transition: left 0.2s ease-in-out, background 0.2s ease;
}

.filter__label--country::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #ffffff;
  transition: left 0.2s ease-in-out, background 0.2s ease;
}

.filter__label--country:hover.filter__label--country::before,
.filter__label--country:focus.filter__label--country::before {
  border: 2px solid #9070ec;
  background: rgba(120, 89, 207, 0.25);
}

.filter__checkbox:checked + .filter__label--country:before {
  background: #7859cf;
  border: none;
}

.filter__checkbox:checked + .filter__label--country:after {
  left: 20px;
}

.filter__checkbox:disabled + .filter__label--country {
  color: #bdbdbd;
  cursor: auto;
}

.filter__checkbox:disabled + .filter__label--country::before {
  background-color: #e2e2e2;
  border: none;
}

.filter__checkbox:checked:disabled + .filter__label--country::after {
  content: "";
  background-color: #bdbdbd;
}

