  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .product__link-image {
    margin-bottom: 27px;
  }
}

.product__image {
  order: -1;
  width: 130px;
  height: 188px;
}

.product__price-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.product__price {
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

/* ================= Sort */
.sort {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
}
@media (min-width: 768px) {
  .sort {
    justify-content: flex-end;
  }
}

.sort__title {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  margin: 0;
}
@media (min-width: 768px) {
  .sort__title {
    margin-right: 11px;
  }
}

.sort__type {
  position: relative;
  border: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  background-color: transparent;
  text-transform: lowercase;
  cursor: pointer;
  padding: 4px 30px 4px 10px;
  border-radius: 4px;
}

.sort__type::after {
  content: "";
  position: absolute;
  background-image: url("../img/decorative/arrow-bottom-sort.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  width: 10px;
  height: 10px;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.sort__type:hover,
.sort__type:focus {
  background-color: #f0ebff;
}

.sort__type:active {
  background-color: transparent;
}

.sort__type.show-sort-list.sort__type::after {
  transform: translateY(-55%) rotate(180deg);
  top: 55%;
}

.sort__list {
  position: absolute;
  right: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  top: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: #ffffff;
  text-transform: lowercase;
  border-radius: 4px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  /* Скрываем варианты сортировки */
  opacity: 0;
  z-index: -5;
  visibility: hidden;
  transition: all 0.3s ease-out;
}

/* Показываем варианты сортировки */
.sort__list.show-sort-list {
  opacity: 1;
  visibility: visible;
  z-index: 5;
  top: calc(100% + 10px);
}

.sort__item {
  border-bottom: 1px solid #f0ebff;
}

.sort__item:last-child {
  border-bottom: none;
}

.sort__link {
  display: block;
  text-decoration: none;
  color: #333333;
  padding: 8px 14px;
}

.sort__link:hover,
.sort__link:focus {
  background-color: #f0ebff;
}

/* Выбранный тип сортировки */
.sort__link--current {
  background-color: #7859cf;
  color: #ffffff;
}

/* Отмена подсвечивания при наведении у выбранного типа сортировки */
.sort__link--current:hover,
.sort__link--current:focus {
  background-color: #7859cf;
}

/* ==================  Catalog - product */
.catalog__product {
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .catalog__product {
    padding-bottom: 100px;
  }
}
@media (min-width: 1440px) {
  .catalog__product {
    padding-bottom: 0;
  }
}

/* ================ Map */
.map__interactive {
  position: relative;
  height: 420px;
}
@media (min-width: 768px) {
  .map__interactive {
    height: 480px;
  }
}
@media (min-width: 1440px) {
  .map__interactive {
    height: 540px;
  }
}

.map__interactive::after,
.map__interactive::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}

.map__interactive::after {
  width: 100%;
  height: 420px;
  background-image: url("../img/map/map-background.png");
  background-position: center top;
}
@media (min-width: 768px) {
  .map__interactive::after {
    height: 480px;
    background-image: url("../img/map/map-background-tablet.png");
  }
}
@media (min-width: 1440px) {
  .map__interactive::after {
    height: 540px;
    background-image: url("../img/map/map-background-desktop.png");
  }
}

.map__interactive::before {
  background-image: url("../img/map/map-pin.svg");
  background-position: center;
  background-position: 0 0;
  width: 38px;
  height: 50px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
@media (min-width: 768px) {
  .map__interactive::before .map__interactive::before {
    left: 52%;
  }
}
@media (min-width: 1440px) {
  .map__interactive::before {
    left: 51%;
    transform: translate(-62%, -50%);
  }
}

/* ================== Copyright */
.copyright {
  margin-top: 11px;
  margin-bottom: 30px;
}
@media (min-width: 1440px) {
