  color: #bdbdbd;
  text-decoration: none;
}

/* Стили при наведении на ссылку 'HTML Academy' */
.copyright__link:hover,
.copyright__link:focus {
  color: #7859cf;
}

.copyright__link:active {
  color: #593cac;
}

.copyright__icon {
  fill: #bdbdbd;
  margin-left: 4px;
}

/* Стили у логотипа при наведении на ссылку 'HTML Academy' */
.copyright__link:hover > .copyright__icon,
.copyright__link:focus > .copyright__icon {
  fill: #7859cf;
}

.copyright__link:active > .copyright__icon {
  fill: #593cac;
}

